

.page-cont{
    height: 100vh;
    width: 100vw;
    /* background-color: rebeccapurple; */
    background-size: cover;
    display: flex;
    flex-direction: row;
}

@media(min-width: 640px)
{
    .header{
        color: white;
        height: 100px;
        width: 100vw;
        display: flex;
        /* flex-direction: column;
        align-items: center; */
        /* background-color: royalblue; */
    }

}

@media(max-width: 640px)
{
    .header{
        color: white;
        padding-top: 20px;
        height: 100px;
        width: 100vw;
        display: flex;
        /* flex-direction: column;
        align-items: center; */
        /* background-color: royalblue; */
    }

}


.header .nav-link{
    width: 350px;
    /* background-color: gold; */
}

.header .title{
    height: 100%;
    padding: 20px;
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Bree Serif';
    font-size: 45px;
    /* background-color: blue; */
}

.header .dld-cont{
    height: 100px;
    position: relative;
    padding: 35px;
    /* background-color: gold; */
}


.download:hover{
    background-color: #c62a88;
    cursor: pointer;
}

.download{
    border-radius: 20px;
    background-color: transparent;
    color: white;
    outline: none;
    border: solid 1px white;
    padding: 10px;
    font-family: 'Bree Serif';
    font-size: 15px;
}

@media(max-width: 640px)
{
    .content{
        height: 60vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: white;
        font-family: 'Bree Serif';
    }

    .content{
        height: 60vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: white;
        font-family: 'Bree Serif';
    }

    .content .divider{
        height: 1px;
        width: 50%;
        margin: 20px;
        background-color: white;
    }

    .content .top{
        font-size: 35px;
        text-align: center;
    }
    
    .cont-bot{
        width: 50vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bot{
        display: flex;
    }

    .inner{
        font-size: 20px;
        width: 250px;
    }
}

@media(min-width: 640px)
{
    .cont-content{
        width: 100vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .content{
        height: 60vh;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: white;
        font-family: 'Bree Serif';
    }

    .content .divider{
        height: 1px;
        width: 50%;
        margin: 20px;
        background-color: white;
    }

    .content .top{
        font-size: 35px;
        text-align: center;
    }
    
    .cont-bot{
        width: 50vw;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bot{
        display: flex;
    }

    .inner{
        font-size: 20px;
        width: 250px;
    }
}

.footer{
    background-color: #ffffff2f;
    display: flex;
    flex-direction: column;
    align-items: center;
    bottom: 0px;
    position: absolute;
    width: 100vw;
}