
*,
*:after,
*:before {
  margin:0;
  padding:0;
  box-sizing:border-box;
}

::-webkit-scrollbar{
    width: 0px;
}


body{
  background-image: url('../assets/images/bg.jpg');
  /* background-size: auto; */
  /* height: 100vh; */
    /* overflow: hidden; */
}

@media (min-width: 640px){
  
  .page-link{
    height: 100px;
    padding: 50px;
    width: 50vw;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

}

li{
  list-style: none;
  display: inline;
  padding: 10px;
  border-radius: 10px;
  
}

li:hover{
  background-color: #c62a88;
}

 a{
  color: whitesmoke;
  text-decoration: none;
  font-family: 'Bree Serif';
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


@media (max-width:640px){
  h1{
    font-size: 30px;
  }
  .page-link{
    margin: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .footer{
    position: sticky;
  }

  .Paypal{
    width: 150px;
    height: 450px;
  }
  
  body{
    height: 100vh;
    width: 100vw;
  }
}